<template>
	<div>
		<!-- 导航栏 -->
		<v-header></v-header>
		<!-- 倒计时 -->
		<div class="count-down fz12"><i class="icon ico-countDowm"></i>倒计时15:15</div>
		<!-- 内容 -->
		<div class="main-box bg-f ">
			<!-- 标题 -->
			<div class="title-bg fz16">单选</div>
			<!-- 题目 -->
			<div class="fz14">
				<p class="subject">题目内容题目内容题目内容题目内容题目内容</p>
				<img src="@/assets/image/today2.png" width="50%"/>
				<!-- 答案 -->
				<div class="answer-list">
					<ul class="answer-box">
						<li class="relative">
							<input type="radio" name="answer" id="answer1"/>
							<label for="answer1">
								<i class="ico-answer">A</i>
								<p class="border-buttom">题目内容题目内容题目内容题目内容题目内容</p>
							</label>
						</li>
						<li class="relative">
							<input type="radio" name="answer" id="answer1"/>
							<label for="answer1">
								<i class="ico-answer">A</i>
								<p class="border-buttom">题目内容题目内容题目内容题目内容题目内容题目内容题目内容题目内容题目内容题目内容</p>
							</label>
						</li>
						<li class="relative">
							<input type="radio" name="answer" id="answer1"/>
							<label for="answer1">
								<i class="ico-answer">A</i>
								<p class="border-buttom">答案</p>
							</label>
						</li>
					</ul>
				</div>
				<button class="btn-empty-big">下一题</button>
				<router-link to="/achievement">成绩</router-link>
			</div>
		</div>
		<!-- 底部题目数量 -->
		<v-nextFooter></v-nextFooter>
	</div>
</template>

<script>
	export default {
		name: 'mock',
		data() {
			return {
				
			};
		}
	}
</script>

<style lang="scss" scoped="scoped">
	.count-down{
		position: fixed;
		top: 18px;
		right: 15px;
		z-index: 6;
		color: $warm-color;
		
		.ico-countDowm{
			width: 12px;
			height: 12px;
			background: url(data:image/gif;base64,) no-repeat center;
			background-size: cover;
			display: inline-block;
			vertical-align: middle;
			margin-right: 2px;
		}
	}
	.main-box{
		padding: 12px 15px;
		
		.title-bg{
			display: inline-block;
			margin: 10px 0 20px ;
		}
		
		.subject{
			padding: 0 20px;
		}
		img{
			margin: 10px auto;
			display: inherit;
		}
		
		.answer-list{
			width: 85%;
			background: url(data:image/gif;base64,) no-repeat top,
						url(data:image/gif;base64,) no-repeat bottom;
			background-size: 100% auto;
			padding: 40px 0;
			box-sizing: border-box;
			margin: 0 auto;
			
			.answer-box{
				padding: 0 46px;
				background: url(data:image/gif;base64,) no-repeat top left,
							url(data:image/gif;base64,) no-repeat top right;
				background-size: auto 90%; 
			}
			
			li{
				padding-left: 47px;
				
				.ico-answer{
					position: absolute;
					width: 20px;
					height: 20px;
					border: 1px solid $gray-c;
					background: #d3d3d3;
					left: 0;
					text-align: center;
					border-radius: 50%;
					vertical-align: middle;
					top: 4px;
				}
				
				p{
					padding: 4px 0;
				}
				
				input[type="radio"]:checked+label{
					.ico-answer{
						background: $main-color url(data:image/gif;base64,) no-repeat center;
						background-size: cover;
						border-color: $main-color;
						text-indent: -9999px;
					}
				}
			}
		}
		
		.btn-empty-big{
			width: 80%;
			margin: 20px auto;
		}
	}
	
</style>



